@import "colors";

.home{
    width: 100%;
    height: 100vh;
    background-color: $second;
    border: 0;
   // border-radius: 0 0 200px 200px;

    >main{
        position: absolute;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        width: 100%;
        height: 100%;
        padding: 2rem;
        justify-content: center;

    h1{
        font: 400 6rem $text1;
        color: $first;
        font-family: 'Times New Roman', Times, serif;

 
    }
    p{
        font:900 1rem $text1;
        color: $first;
        font-family: 'Times New Roman', Times, serif;
    }

    
}
&::before{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    background: no-repeat center/auto url(../assets/1.png);
    filter :blur(3px);
    border-radius: 0 0 200px 200px;
}
}
.home2{
    height: 100vh;
    width: 100%;
    background-color: $second;
    padding: 500px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -200px;
    
    img{
        width: 60%;
        animation: imgup 0.7s linear infinite alternate;
    }
    p{
        color:$first;
        font: 100 1rem $text2 ;
        letter-spacing: 2px;
        word-wrap: 5px;
        text-align: justify;
        margin-right: 50px;
    }
}

    >div{
        background-color: $first;
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content:space-around;
        align-items: center;
        padding: 5rem;
        height: 100vh;
    }
    
@keyframes imgup {
    to{
        transform:translateY(-10px);
    }
}

.home3{
    width: 100%;
    height: 120vh;
    background-color: $second;
    display: flex;
    margin-top: -200px;
    //justify-content: center;
    //align-items: center;
    
    >div{
        width: 70%;
        height: 100vh;
        background-color: $first;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 0 200px 200px 0;
        >h1{
            text-transform: uppercase;
            font: 600 2rem $text2;
            border-bottom: 6px solid black;
            padding: 2px;
            margin-bottom: 5px;
        }
        >p{
            letter-spacing: 0.1rem;
            word-spacing: 0.2rem;
            padding: 4rem;
            text-align: justify;
            font: italic 100 1.2rem $text2;
        }
        

        
    }
}
.home4{
    width: 100%;
    height: 100vh;
    background-color: $second;
    display: flex;
    justify-content: flex-end;  

    >div{
        background-color: $third;
        border-radius: 200px 0 0 200px;
        width: 70%;
        height: 70vh;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        >h1{
            text-transform: uppercase;
            font-family: 'Times New Roman', Times, serif;
            color: $first;
            width: 140px;
            border-bottom: 2px solid $first;
            padding: 0.5rem;
        }

        >article{
            display: flex;
            margin: 2rem 0; 
            flex-wrap: wrap;
            justify-content: center;

            >div{
                margin: 1rem;
                width: 10rem;
                height: 10rem;
                border-radius: 50%;
                background-color: $first;
                animation: imgup 0.7s linear infinite alternate;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                color: $dark;
                cursor: pointer;
                transition: all 0.5;
                &:hover{
                    filter: invert(1);
                }
                >svg{
                    font-size: 4rem;
                }
                >p{font: 400 1.2rem $text2;}

            }
        }

    }
}
