@import 'colors.scss';
.contact{
    height: 100vh;
    width: 100%;
    background-color: $second;
    display: flex;
    justify-content: center;
    align-items: center;

    >main{
        background-color: $first;
        width: 70%;
        height: 40rem;
        border-radius: 0 200px 200px 0;
        
        >form{

            margin: 5rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border-radius: 50px 0 50px 0;

            >div{
                height: 3rem;
                width: 60%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                margin:0.5rem;

                >label{
                    font: 400 1rem $text1;
                    width: 20%;
                    margin: 0.1rem;
                }
                >input{
                    align-self: stretch;
                    width: 80%;
                    padding: 1rem;
                    border-radius: 5px;
                    border: 1px solid rgba(123,123,123,0.6);
                    outline: none;
                }
                
            }
            >button{
                background-color: $second;
                color: $first;
                width: 200px;
                margin: 3rem 0;
                padding: 0.5rem;
                border: none;
                border-radius: 5px;
                font: 500 1rem $text1;
                &:hover{
                  opacity: 0.9;  
                }
            
            }

        }
    }

}
