@import "./colors";

footer{
    background-color: $first;
    width: 100%;
    height: 200px;
    color: $dark;
    display: grid;
    grid-template-columns: 4fr 1fr;
    padding: 2rem;

    >div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        h2{
            font: 700 2rem $text1;
            text-transform: uppercase;
            color: $third;
        }
        p{
            font: 400 1.2rem $text1;

        }
        h5{
            font: 300 2rem $text1;
            margin: 1rem 0 ;
            text-align: center;
        }
        >div{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            a{
                color: $dark;
                font: 800 1rem $text2;
                &:hover{
                    color: $third;
                }
            }
        }
        
    }
}