@media screen and (max-width:820px) 
{
   .home3{
    height: 130vh;
    >div{
        height: 120vh;
    }
   }
   .contact > main{
    width: 100%;
    height: 100vh;
    border-radius: 0;
    h1{
        text-align: center;
    }
   } 
}

@media screen and (max-width:700px) {
    nav{
        >h1{
            display: none;
        }
        >main{
            justify-content: center;
            width: 100%;
            
        >a{
            margin: 0.3rem;
            }
        }
    }


    .home > main{

        align-items: center;

        >h1{
            font-size: 3rem;
        }
        p{
            font-size: 0.8rem;
        }
    }
    .home{
        &::before{
            border-radius: 0;
        }
    }

    .home2{
        height: 120vh;
        flex-direction: column;

        >img {
            margin-top: -10rem;
            width: 100%;
        }
        >p{
            margin: 0 1rem 0 1rem;
            letter-spacing: normal;
            
        }
    }

    .home3{
        height: 100vh;
        div{
            width: 100%;
            text-align: center;
            border-radius: 0;
            >h1{
                margin-top: -120px;
            }
            >p{
                padding: 5rem 2rem;
                font-size: 0.9rem;
                line-height: 150%;
                font-family:'Times New Roman', Times, serif;
                word-spacing: normal;
                letter-spacing: normal;
            }
    }

    }
    .home4{
        >div{
            width: 100%;
            height: 100vh;
            border-radius:0 0 0 0;
        >article{
            margin: 0.5rem;
            >div{
            height: 8rem;
            width: 8rem;
            margin: 0.8rem;
        }
    }
    }
    }
    .contact{
        margin-top: 120px;
        main{
            width: 100%;
            padding: 1rem;
            >h1{
                margin: 1rem;
            }
            >form{
                margin: 0%;
                >div{
                    width: 100%;
                }
            }
        }
    } 

    footer{
        height: 50vh;
        grid-template-columns: 1fr;
        padding: 5rem;
        >div{
        >p{font-size: 0.7rem;}
            align-items: center;
        }
        
    }
}